import { useCallback } from 'react';
import useLocalStorage from './useLocalStorage';

const DROPPABLE_IDS = {
    PLAYER_LIST: 'player-list',
    WAITING_AREA: 'waiting-area'
  };

  const PLAYER_ID_PREFIX = 'player-';

const useTableAssignment = () => {
  const [tables, setTables] = useLocalStorage('tables', []);
  const [players, setPlayers] = useLocalStorage('players', []);
  const [waitingPlayers, setWaitingPlayers] = useLocalStorage('waitingPlayers', []);

  // Add a new player
  const addPlayer = useCallback((name) => {
    const id = Date.now().toString();
    const newPlayer = {
      id: `${PLAYER_ID_PREFIX}${id}`,
      name
    };
    // Change setPlayers to setWaitingPlayers
    setWaitingPlayers(prev => [...prev, newPlayer]);
  }, [setWaitingPlayers]); // Update dependency array

  // Add a new table
  const addTable = useCallback(() => {
    const newTable = {
      id: Date.now().toString(),
      players: []
    };
    setTables(prev => [...prev, newTable]);
  }, [setTables]);

  // Delete a table
  const deleteTable = useCallback((tableId) => {
    setTables(prev => {
      const tableToDelete = prev.find(table => table.id === tableId);
      if (tableToDelete) {
        // Move players back to waiting area
        setWaitingPlayers(wp => [...wp, ...tableToDelete.players]);
      }
      return prev.filter(table => table.id !== tableId);
    });
  }, [setTables, setWaitingPlayers]);

  // Delete a player
  const deletePlayer = useCallback((playerId) => {
    setPlayers(prev => prev.filter(player => player.id !== playerId));
    setWaitingPlayers(prev => prev.filter(player => player.id !== playerId));
    setTables(prev => 
      prev.map(table => ({
        ...table,
        players: table.players.filter(player => player.id !== playerId)
      }))
    );
  }, [setPlayers, setWaitingPlayers, setTables]);

  // Handle drag end
  const handleDragEnd = useCallback((result) => {
    const { destination, source, draggableId } = result;
  
    if (!destination) return;
  
    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

  // Find the player being dragged
  const sourcePlayer = players.find(p => p.id === draggableId) ||
                      waitingPlayers.find(p => p.id === draggableId) ||
                      tables.flatMap(t => t.players).find(p => p.id === draggableId);

  if (!sourcePlayer) return;
  

  // Remove from source
  if (source.droppableId === 'player-list') {
    setPlayers(prev => prev.filter(p => p.id !== sourcePlayer.id));
  } else if (source.droppableId === 'waiting-area') {
    setWaitingPlayers(prev => prev.filter(p => p.id !== sourcePlayer.id));
  } else if (source.droppableId.startsWith('table-')) {
    const sourceTableId = source.droppableId.replace('table-', '');
    setTables(prev => prev.map(table => {
      if (table.id === sourceTableId) {
        return {
          ...table,
          players: table.players.filter(p => p.id !== sourcePlayer.id)
        };
      }
      return table;
    }));
  }


    // Add to destination
    if (destination.droppableId === 'waiting-area') {
        setWaitingPlayers(prev => [...prev, sourcePlayer]);
      } else if (destination.droppableId === 'player-list') {
        setPlayers(prev => [...prev, sourcePlayer]);
      } else if (destination.droppableId.startsWith('table-')) {
        const destTableId = destination.droppableId.replace('table-', '');
        setTables(prev => prev.map(table => {
          if (table.id === destTableId) {
            const newPlayers = [...table.players];
            newPlayers.splice(destination.index, 0, sourcePlayer);
            return {
              ...table,
              players: newPlayers
            };
          }
          return table;
        }));
      }
    }, [players, waitingPlayers, tables, setPlayers, setTables, setWaitingPlayers]);

  // Randomly assign waiting players to tables
  const randomAssign = useCallback(() => {
    if (tables.length === 0 || waitingPlayers.length === 0) return;

    const shuffledPlayers = [...waitingPlayers].sort(() => Math.random() - 0.5);
    const playersPerTable = Math.ceil(shuffledPlayers.length / tables.length);
    
    const newTables = [...tables].map(table => ({
      ...table,
      players: []
    }));

    shuffledPlayers.forEach((player, index) => {
      const tableIndex = Math.floor(index / playersPerTable);
      if (tableIndex < newTables.length) {
        newTables[tableIndex].players.push(player);
      }
    });

    setTables(newTables);
    setWaitingPlayers([]);
  }, [tables, waitingPlayers, setTables, setWaitingPlayers]);

  // Reset functions
  const resetTables = useCallback(() => {
    const playersFromTables = tables.flatMap(table => table.players);
    setWaitingPlayers(prev => [...prev, ...playersFromTables]);
    setTables([]);
  }, [tables, setWaitingPlayers, setTables]);

  const resetPlayers = useCallback(() => {
    setPlayers([]);
    setWaitingPlayers([]);
    setTables(prev => prev.map(table => ({ ...table, players: [] })));
  }, [setPlayers, setWaitingPlayers, setTables]);

  const resetAll = useCallback(() => {
    setPlayers([]);
    setWaitingPlayers([]);
    setTables([]);
  }, [setPlayers, setWaitingPlayers, setTables]);

  return {
    tables,
    players,
    waitingPlayers,
    addPlayer,
    addTable,
    deleteTable,
    deletePlayer,
    handleDragEnd,
    randomAssign,
    resetTables,
    resetPlayers,
    resetAll
  };
};

export default useTableAssignment;
export { DROPPABLE_IDS };